"use client";
import React from "react";
// Next
import Image from "next/image";
import Link from "next/link";
import { useRouter } from "next/navigation";
// @Mui
import { Box, Drawer, List, ListItem, Divider, styled } from "@mui/material";
//
import Logo from "../Logo";
import CustomButton from "../CustomButton";

// ------------------------------------------------------------------------------------------------------------------

function SideDrawer({ isTriggered, closeHandler, navLinks }) {
  const NavLink = styled(Link)(({ theme }) => ({
    ...theme.typography.subtitle1,
    color: theme.palette.text.primary,
    textDecoration: "none",
  }));

  const { push } = useRouter();

  return (
    <Drawer anchor="left" open={isTriggered} onClose={closeHandler}>
      <Box
        style={{ width: 250 }}
        role="presentation"
        onClick={closeHandler}
        onKeyDown={closeHandler}
      >
        {/* Logo Section */}
        <Box sx={{ padding: "16px", textAlign: "center" }}>
          {/* Replace with your logo */}
          <Image />
        </Box>

        <Divider />

        {/* Drawer Items */}
        <List>
          {navLinks.map((link, index) => (
            <NavLink key={index} href={link.href} passHref>
              <ListItem
                button
                component="a"
                sx={{ display: "flex", justifyContent: "center" }}
              >
                {link.title}
              </ListItem>
            </NavLink>
          ))}
        </List>
        <Divider sx={{ my: 2 }} />
        <Box sx={{ display: "flex", justifyContent: "center" }}>
          <CustomButton
            label="Contact Us"
            bgColor="grey.0"
            color="secondary.main"
            onClick={() => push("/contact-us")}
          />
        </Box>
      </Box>
    </Drawer>
  );
}

export default SideDrawer;
