"use client";
import React, { useState } from "react";
// next
import Image from "next/image";
import { useRouter, usePathname } from "next/navigation";
// @Mui
import {
  Box,
  Button,
  Container,
  Divider,
  IconButton,
  Stack,
  Tooltip,
  useMediaQuery,
  useTheme,
} from "@mui/material";
//
// import Logo from "../Logo";
import HeaderLink from "./HeaderLink";
import { Icon } from "@iconify/react";
import SideDrawer from "./SideDrawer";
// assets
import logo from "@/assets/logo.png";
import logo2 from "@/assets/logo2.png";
import CustomButton from "../CustomButton";

// ---------------------------------------------------------------------

function Header() {
  const [sideDrawer, triggerSideDrawer] = useState(false);

  const HEADER_LINKS = [
    { title: "Home", href: "/" },
    { title: "What We Do", children: [], href: "/what-we-do" },
    { title: "Projects", href: "/projects" },
    { title: "Lab", href: "/lab" },
    { title: "Why SPS", href: "/why-sps" },
    { title: "Careers", href: "/careers" },
  ];

  const theme = useTheme();

  const isMdOrLarger = useMediaQuery(theme.breakpoints.up("md"));

  const { push } = useRouter();

  const pathname = usePathname();

  return (
    <Box
      sx={{
        py: 1,
        position: "fixed",
        top: 0,
        zIndex: 10,
        width: "100%",
        bgcolor: "white",
      }}
    >
      <Container maxWidth="xl">
        <Stack
          direction="row"
          justifyContent={isMdOrLarger ? "space-between" : "center"}
          alignItems="center"
        >
          {isMdOrLarger && (
            <Box>
              <Image src={logo} alt="logo" />
            </Box>
          )}
          <Box>
            <Image src={logo2} alt="logo" />
          </Box>
          {isMdOrLarger && pathname !== "/contact-us" && (
            <Box>
              <CustomButton
                color="secondary.main"
                bgColor="grey.0"
                label="Contact Us"
                onClick={() => push("/contact-us")}
              />
            </Box>
          )}
        </Stack>
        <Divider sx={{ my: 2 }} />
        <Stack direction="row" justifyContent="center" alignItems="center">
          {/* Header Links */}
          {isMdOrLarger && (
            <Stack direction="row" alignItems="center" gap={15}>
              {HEADER_LINKS.map((_, index) => (
                <HeaderLink key={index} title={_.title} href={_.href} />
              ))}
            </Stack>
          )}
          {/* Actions */}
          <Stack direction="row" alignItems="center" gap={1}>
            {!isMdOrLarger && (
              <Tooltip title="Menu">
                <IconButton onClick={() => triggerSideDrawer(!sideDrawer)}>
                  <Icon icon="jam:menu" style={{ width: 30, height: 30 }} />
                </IconButton>
              </Tooltip>
            )}
          </Stack>
        </Stack>
      </Container>
      <SideDrawer
        isTriggered={sideDrawer}
        closeHandler={() => triggerSideDrawer(false)}
        navLinks={HEADER_LINKS}
      />
    </Box>
  );
}

export default Header;
